<script>
  import { Toast } from 'vant';
  import {handleClipboard} from '../../../utils/clipboard'
  import vueQr from 'vue-qr'
export default {
  name: 'recharge',
  data () {
    return {
      // url
      discUrl:'',
      // uid
      userId:'',
      // token
      token:'',
      channel:null,
      // 汇率
      rate:'',
      // 约合美金
      peaceTreaty:0,
      // 提现金额
      money: '',
      // 选择支付方式
      sexValue: '',
      sexValueIdx:0,
      bankName:"",
      // 支付方式
      paymentList:[],
      formDataList:{},
      formData:{},
      Url:'',
      action:'',
      paymentData:{},
      disabledPay:false,
      payOff:true,
      link:[
        '0x412425619234f23de1d62bd965ac54831651b2f5',
        '1Nsnw9fegYxJS2wZQ3rsRQuNxxZY558fX2',
        'TLyrgfEFG7Zr9jG4vMnttPEBh7SzXSadUw',
      ],
      link1:[
        '1MGNAm1P2o3jT2rWbVKb8X5VSsttbSz4Dr',
      ],
      value1: 0,
      option1: [
      ],

    }
  },
  components: {
    vueQr
  },
  created(){
    this.discUrl = localStorage.getItem('discUrl')
    // this.localSocket()
    // this.discUrl = '192.168.5.14'
    // this.discUrl = '192.168.5.28'
    this.token =  localStorage.getItem('token')
    this.userId = localStorage.getItem('uid')

    this.getPayment()
  },
  methods: {
    // 复制
    copyNumber (link) { // 物流单号复制
      handleClipboard(link, event, () => {
        alert(this.$t('CP1'))
      }, () => {
        alert(this.$t('CP2'))
      })
    },
    // 返回上级
    linkHome () {
      this.$router.push({
        name:'my'
      })
    },
    // 获取支付方式
    getPayment(){
      this.$axios.get('http://'+this.discUrl+':7777/api/pay/configs?userId='+this.userId+'&token='+this.token).then(res =>{
        // console.log(res)
        if(res.data.object != null){
          console.log(res.data.object.channels)
          let dataList = res.data.object.channels
          for(let i=0,len = dataList.length;i<len;i++){
            let data = {}
            data.text = dataList[i].paychannel
            data.value = i
            this.option1.push(data)
          }
          console.log(this.option1)
          this.paymentList = res.data.object.channels
          this.rate =  res.data.object.rate
        }else {
          Toast({
            message:res.data.msg,
            duration:1000,
            position:'bottom'
          })
        }

      })
    },
    // 选择支付方式
    changePay(idx){
      if(this.money !== ''){
        Toast.loading({
          message:'正在确认支付信息...',
          forbidClick:true,
          duration:0
        })
        this.sexValue = this.paymentList[idx].payname
        let channel = this.paymentList[idx].paychannel
        let that = this
        let postData = {
          "userId":that.userId,
          "token":that.token,
          "bankName":"",
          "money":that.money,
          "type":1
        }
        this.$axios.post('http://'+this.discUrl+':7777/api/pay/preorder/'+channel,postData).then(res =>{
          console.log(res)
          Toast.clear()
          if(res.data.code === 0){
            this.paymentData = JSON.parse(res.data.object)
            console.log(this.paymentData)
            if(this.paymentData.status === "error"){
              Toast({
                position:'bottom',
                message:that.paymentData.msg,
                duration:1000,
                onClose() {
                  that.money = ''
                  that.sexValue = ''
                }
              })
            }
          }
        })
      }else {
        Toast({
          duration:1500,
          message:'请输入充值金额！',
          position:'bottom'
        })
      }

    },
    // 约合美金
    changeMoney(){
      this.peaceTreaty = (this.money/this.rate).toFixed(2)
    },
    // 确认充值
    ConfirmRecharge(){
      let that = this
      console.log(this.option1[this.value1].text)
      let option1 = this.option1[this.value1].text
      let postData = {
        "userId":that.userId,
        "token":that.token,
        "bankName":"",
        "money":that.money,
        "type":1
      }

      if(this.money !== ''){
        if(this.value1 === 0){
          this.$axios.post('http://'+this.discUrl+':7777/api/pay/preorder/'+option1,postData).then(res =>{
            console.log(res)
            if(res.data.code === 0){
              this.$router.push({
                name:'rechargeBTC',
                params:{
                  data:this.link1,
                  title:option1
                }
              })
            }
          })
        }else {
          this.$axios.post('http://'+this.discUrl+':7777/api/pay/preorder/'+option1,postData).then(res =>{
            console.log(res)
            if(res.data.code === 0){
              this.$router.push({
                name:'rechargeUSDT',
                params:{
                  data:this.link,
                  title:option1
                }
              })
            }
          })
        }
      }else {
          Toast({
            message:this.$t('IRAmount'),
            position:'bottom',
            duration:2000
          })
      }

      // Toast({
      //   message:'此功能暂未开通',
      //   position:'bottom',
      //   duration:2000
      // })
      // if(this.paymentData.status ===  "success"){
      //   this.$router.push({
      //     name:'UnionPay',
      //     params:{
      //       data:this.paymentData.data
      //     }
      //   })
      // }else if(that.paymentData.msg !== ''){
      //   Toast({
      //     message:'确认支付信息失败',
      //     position:'bottom',
      //     duration:1000
      //   })
      // }
    },
  }
}
</script>

